* {
  margin: 0;
  padding: 0;
  font-family: 'H', 'Trebuchet MS', Helvetica;
}

@font-face {
  font-family: 'H';
  src: url(./fonts/H-Bold.eot);
  src: url(./fonts/H-Bold.woff2) format("woff2"), url(./fonts/H-Bold.woff) format("woff"), url(./fonts/H-Bold.ttf) format("truetype");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'H';
  src: url(./fonts/H-Medium.eot);
  src: url(./fonts/H-Medium.woff2) format("woff2"), url(./fonts/H-Medium.woff) format("woff"), url(./fonts/H-Medium.ttf) format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'H';
  src: url(./fonts/H-Book.eot);
  src: url(./fonts/H-Book.woff2) format("woff2"), url(./fonts/H-Book.woff) format("woff"), url(./fonts/H-Book.ttf) format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'H';
  src: url(./fonts/H-Light.eot);
  src: url(./fonts/H-Light.woff2) format("woff2"), url(./fonts/H-Light.woff) format("woff"), url(./fonts/H-Light.ttf) format("truetype");
  font-weight: 300;
  font-style: normal;
}

body {
  color: white;
  width: 100%;
  height: 100%;

  font-size: 16px;
  word-break: break-word;
  font-weight: 400;
  font-style: normal;
}

div.content {
  background-image: linear-gradient(109.6deg, rgba(53,65,140,1) 0%, rgba(92,169,196,1) 100%);
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  text-align: center;
}

div.logo {
  height: 100px;
  margin: auto;
  text-align: center;
}

div.logo svg {
  width: 180px;
  height: 100px;
}

button {
  margin-top: 50%;
  padding: 20px 40px;
  border: 1px solid #fff;
  border-radius: 16px;
  background: none;
  font-size: 20px;
  color: #fff;
  cursor: pointer;
  opacity: 0.7;
}

button:hover {
  background-color: rgba(255,255,255,0.08);
  opacity: 0.9;
}

div.game p {
  margin-top: 30px;
}

div.game div.emoji {
  font-size: 60px;
}

div.game input[type=text] {
  margin-top: 15px;
  width: 80px;
  height: 60px;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: rgba(255,255,255,0.08);
  border: 3px solid rgba(204,0,0,0);
  border-bottom: 3px solid #43c0d1;
  font-size: 40px;
  text-align: center;
  margin-top: 15px;
  transition: border-color 1s ease;
}

div.game input[type=text]:focus,
div.game input[type=text]:hover {
  background-color: rgba(255,255,255,0.15);
  border-bottom-color: #43c0d1;
}

div.game input[type=text].error,
div.game input[type=text].error:focus,
div.game input[type=text].error:hover {
  border-left-color: rgba(204,0,0,1);
  border-right-color: rgba(204,0,0,1);
  border-top-color: rgba(204,0,0,1);
}

div.game div.time {
  opacity: 0.5;
  margin-top: 40px;
  font-family: 'Roboto Mono', monospace;
}

div.results p {
  margin-top: 40px;
  margin-bottom: 10px;
  opacity: 0.6;
}

div.results span.time {
  font-size: 50px;
  opacity: 0.85;
}

div.results button {
  margin-top: 80px;
}
